.overlay {
  width: 100vw;
  height: 100vh;
  background-color: hsla(0, 0%, 0%, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}
img {
  max-width: 100%;
}
*:focus:focus-visible {
  outline: solid rgb(25, 255, 190) 1px;
}
